            this.sabbathmode = Number(deviceProps.SabbathMode)
            this.sabbathModeStatus = Number(deviceProps.SabbathModeStatus)
            this.getLightsInfo();
          }
          else if("data" in this.proList && this.proList.data.length == 0){
            this.getLightsInfo();
          }
        } else {
