          this.SwitchModeOntime = index + 6;
        }else{
          this.SwitchModeOntime = index - 1;
        }
      }
      this.showPop = false;
      this.onforPop = false;
      let params = {
        SwitchModeOntime: this.SwitchModeOntime,
      };
      console.log("点击持续时间传递的入参", params);
      this.$native.sendCmd(
        this.from,
        this.nativeKey,
        this.deviceId,
        JSON.stringify(params)
      );
      if (this.from == "html") {
        let res = {
          resultCode: "0",
        };
        this.sendCmdCallback(res);
      }
