    //获取灯光信息
    getLightsInfo() {
      // this.showLoading = true;
      let type = { deviceId: this.deviceId };
      //刷新数据
      // if(this.from=='ios'){
      //   window.webkit.messageHandlers.getDevicePropertiesInfo.postMessage('')
      // }else if(this.from=='android'){
      //   window.wine.getDevicePropertiesInfo(this.nativeKey, JSON.stringify(type))
      // }

      this.$native.getLightsInfo(
        this.from,
        this.nativeKey,
        JSON.stringify(type)
      );
      if (this.from == "html") {
        this.showLoading = false;
        let lightsInfo = {
          errorDesc: "获灯光信息成功",
          resultCode: "0",
          data: {
            Zone1LightOnoff: "1",
            Zone2LightOnoff: "",
            Zone3LightOnoff: "",
            FunctionalLightsIntensity: "40",
            FunctionalLightsColour: "20",
            AmbientOntime: "0",

            AmbientRGBlightIntensity: "70",
            AmbientRcolour: "0",
            AmbientGcolour: "0",
            AmbientBcolour: "205",
            FunctionalOntime: "2",

            AmbientOntime: "0",
            FunctionalOntime: "0",

            SwitchLightsIntensity: "60", //1 brightness
            SwitchLightsColour: "20",
            SwitchRGBLightIntensity: "50",
            SwitchRColour: "125",
            SwitchGColour: "125",
            SwitchBColour: "125",
            SwitchModeOntime: "5",
            SaveSwitchMode: "1",
            RemoteControlMode: "1",
          },
        };
        this.getLightsCallback(lightsInfo);
      }
    },
    lightFindArray(numbers,val){
    return numbers.indexOf(val) !=-1;
    },
    lightPropertyCongruentOrNot(canUpdateList){
      let lst = [];
      for(var i in canUpdateList){
        lst.push(canUpdateList[i])
      }
      return lst.length > 0 ? lst.every(res => res > 0) : false;
    },
    calculationWhichApplianceOperation(switchInfo){
      console.log('getLightsCallback calculationWhichApplianceOperation 属性是否有变化start*************')
      let oneZoneProperty = this.oneZoneFlag == 0 ? ['SaveSwitchMode','SwitchLightsIntensity','SwitchLightsColour','SwitchRGBLightIntensity','SwitchRColour','SwitchGColour','SwitchBColour','SwitchModeOntime'] : ['SaveSwitchMode','SwitchLightsIntensity','SwitchLightsColour','SwitchModeOntime']
      let canUpdateList = {};
      let flag =true;
      for(var initDSD in switchInfo){
        if(oneZoneProperty.indexOf(initDSD)!=-1){
          if(typeof(this.initDataStateData[initDSD]) == 'undefined'){
            this.initDataStateData[initDSD] = []
          }
          if(this.initDataStateData[initDSD].length <= 3){
            if(this.initDataStateData[initDSD][0] == undefined){
              console.log('getLightsCallback initDataStateData init_data oneZoneFlag = '+ this.oneZoneFlag+' 初始化各个均为 [] 的情况')
              this.initDataStateData[initDSD].push(switchInfo[initDSD])
            }
            // 证明用户做出操作指令
            if(this.initDataStateData[initDSD].length >= 2){
              if(this.initDataStateData[initDSD][0] != undefined && this.initDataStateData[initDSD][1] != undefined ){
                // 需要刷云端的情况  state[1]的数据 与 新返回的属性值相等，直接拉云端数据，清空state
                if(this.initDataStateData[initDSD][1] == switchInfo[initDSD]){
                  console.log('getLightsCallback initDataStateData oneZoneFlag = '+ this.oneZoneFlag+'  state[1] == new',initDSD)
                  this.initDataStateData[initDSD][0] = switchInfo[initDSD]
                  canUpdateList[initDSD] = 1
                }
                // 初始化数据[1] != 新返回的属性值 去判断老的[0]与新返回的属性值是否一致，一致不做操作，不一致清空state拉云端数据
                else if(this.initDataStateData[initDSD][1] != switchInfo[initDSD]){
                  if(this.initDataStateData[initDSD][0] == switchInfo[initDSD]){
                    console.log('getLightsCallback initDataStateData oneZoneFlag = '+ this.oneZoneFlag+'  state[1] != new; state[0] == new',initDSD)
                    canUpdateList[initDSD] = 0
                  }
                  // 需要刷云端的情况
                  if(this.initDataStateData[initDSD][0] != switchInfo[initDSD]){
                    console.log('getLightsCallback initDataStateData oneZoneFlag = '+ this.oneZoneFlag+'  state[1] != new; state[0] != new 其他设备操作了数据',initDSD)
                    this.$store.commit('change_switchLightInfo',{});//云端 app端相等，置空
                    this.initDataStateData = Object.assign({});
                    canUpdateList = Object.assign({})
                    flag = false
                  }
                }
              }
            }
          }
        }
      }

      this.lightSwitchDataCanUpdate = flag ? this.lightPropertyCongruentOrNot(canUpdateList) : false;

      console.log('getLightsCallback calculationWhichApplianceOperation 值是否全相等',this.lightSwitchDataCanUpdate,canUpdateList)
      console.log('getLightsCallback calculationWhichApplianceOperation initDataStateData',JSON.stringify(this.initDataStateData))
      console.log('getLightsCallback calculationWhichApplianceOperation 属性是否有变化end*************')
    },
    //获取灯光信息回调
    getLightsCallback(val) {
      console.log('getLightsCallback -')
      console.log('getLightsCallback --')
      console.log('getLightsCallback ---')
      console.log('getLightsCallback -------------------------start-------------------------')
      console.log("getLightsCallback返回的数据 ", JSON.stringify(val));
      // console.log(val)
      this.showLoading = false;
      if (val.resultCode == "0" || val.resultCode == 0) {
        if ("data" in val) {
          let lightsInfo = val.data;
          let switchInfo = {
            SaveSwitchMode:Number(lightsInfo.SaveSwitchMode) == 1 ? true : false,//灯光 - 开关 - 开关
            SwitchLightsIntensity:Number(lightsInfo.SwitchLightsIntensity),//switch 灯光亮度
            SwitchLightsColour:Number(lightsInfo.SwitchLightsColour),//switch 灯光色温
            SwitchRGBLightIntensity:Number(lightsInfo.SwitchRGBLightIntensity),// switch RGB亮度
            SwitchRColour:Number(lightsInfo.SwitchRColour),// RGB:R
            SwitchGColour:Number(lightsInfo.SwitchGColour),// RGB:G
            SwitchBColour:Number(lightsInfo.SwitchBColour),// RGB:B
            SwitchModeOntime:Number(lightsInfo.SwitchModeOntime)// switch 持续时间
          }
          this.init_data = switchInfo;
          this.calculationWhichApplianceOperation(switchInfo)
          console.log('getLightsCallback this.init_data',this.init_data)
          console.log('getLightsCallback this.switchLight_Info',JSON.stringify(this.switchLight_Info))
          console.log('getLightsCallback this.switchLight_Info state',JSON.stringify(this.switchLight_Info) ==='{}')
          console.log('getLightsCallback this.lightSwitchDataCanUpdate',!this.lightSwitchDataCanUpdate)
          if(JSON.stringify(this.switchLight_Info) ==='{}' && !this.lightSwitchDataCanUpdate){
            console.log('getLightsCallback light switch 默认云端拉数据 以及 无任何操作state为空 拉数据')
            //v12用
            this.SaveSwitchMode =
              Number(lightsInfo.SaveSwitchMode) == 1 ? true : false;

            this.SwitchModeOntime = Number(lightsInfo.SwitchModeOntime) || 0;

            if(this.oneZoneFlag == 0){//三温区
              this.SwitchModeOntimeText = this.presenseSensorDisplayList[
                this.SwitchModeOntime
              ];
            }else{
              console.log("getLightsCallback~~~返回的数据~~~SwitchModeOntime", this.SwitchModeOntime);
              if(this.SwitchModeOntime == 6){
                this.SwitchModeOntimeText = this.presenseSensorDisplayList[0];
              }else{
                this.SwitchModeOntimeText = this.presenseSensorDisplayList[this.SwitchModeOntime + 1];
              }
            }

            this.SwitchLightsIntensity =
              Number(lightsInfo.SwitchLightsIntensity) || 0;
            if (this.SwitchLightsIntensity > 0) {
              this.lightsSwitchOnoff = true;
            }
            console.log('SwitchLightsIntensity',this.SwitchLightsIntensity)
            if(this.oneZoneFlag == 0){//三温区
              this.SwitchLightsColour =
                          23 - Number(lightsInfo.SwitchLightsColour) || 0;
            }else{
              this.SwitchLightsColour = Number(lightsInfo.SwitchLightsColour) || 0;
            }

            this.SwitchRGBLightIntensity =
              Number(lightsInfo.SwitchRGBLightIntensity) || 0;

            if (this.SwitchRGBLightIntensity > 0) {
              this.RGBLightOnoff = true;
            }
            this.SwitchRColour = Number(lightsInfo.SwitchRColour) || 0;
            this.SwitchGColour = Number(lightsInfo.SwitchGColour) || 0;
            this.SwitchBColour = Number(lightsInfo.SwitchBColour) || 0;
            const SwitchColour =
              "rgb(" +
              this.SwitchRColour +
              "," +
              this.SwitchGColour +
              "," +
              this.SwitchBColour +
              ")";
            console.log(SwitchColour);
            this.$store.commit("lights/SWITCH_R_COLOUR", this.SwitchRColour);
            this.$store.commit("lights/SWITCH_G_COLOUR", this.SwitchGColour);
            this.$store.commit("lights/SWITCH_B_COLOUR", this.SwitchBColour);
            this.$store.commit("lights/SWITCH_COLOUR", SwitchColour);
            this.RemoteControlModeFlag = Number(lightsInfo.RemoteControlMode);
          }
          else if(JSON.stringify(this.switchLight_Info) !='{}' && this.lightSwitchDataCanUpdate){
            console.log('getLightsCallback this.init_data',JSON.stringify(this.init_data))
            console.log('getLightsCallback this.switchLight_Info',JSON.stringify(this.switchLight_Info))
            if(this.oneZoneFlag==0){
              if(this.lightSwitchDataCanUpdate){
                // 三温区
                this.SaveSwitchMode = Number(lightsInfo.SaveSwitchMode) == 1 ? true : false;
                this.SwitchModeOntime = Number(lightsInfo.SwitchModeOntime) || 0;
                if(this.oneZoneFlag == 0){//三温区
                  this.SwitchModeOntimeText = this.presenseSensorDisplayList[
                    this.SwitchModeOntime
                  ];
                }else{
                  console.log("getLightsCallback~~~返回的数据~~~SwitchModeOntime", this.SwitchModeOntime);
                  if(this.SwitchModeOntime == 6){
                    this.SwitchModeOntimeText = this.presenseSensorDisplayList[0];
                  }else{
                    this.SwitchModeOntimeText = this.presenseSensorDisplayList[this.SwitchModeOntime + 1];
                  }
                }

                this.SwitchLightsIntensity =
                  Number(lightsInfo.SwitchLightsIntensity) || 0;
                if (this.SwitchLightsIntensity > 0) {
                  this.lightsSwitchOnoff = true;
                }
                if(this.oneZoneFlag == 0){//三温区
                  this.SwitchLightsColour = 23 - Number(lightsInfo.SwitchLightsColour) || 0;
                }else{
                  this.SwitchLightsColour = Number(lightsInfo.SwitchLightsColour) || 0;
                }
                this.SwitchRGBLightIntensity = Number(lightsInfo.SwitchRGBLightIntensity) || 0;

                if (this.SwitchRGBLightIntensity > 0) {
                  this.RGBLightOnoff = true;
                }
                this.SwitchRColour = Number(lightsInfo.SwitchRColour) || 0;
                this.SwitchGColour = Number(lightsInfo.SwitchGColour) || 0;
                this.SwitchBColour = Number(lightsInfo.SwitchBColour) || 0;
                const SwitchColour ="rgb(" + this.SwitchRColour +"," + this.SwitchGColour +"," + this.SwitchBColour +")";
                console.log(SwitchColour);
                this.$store.commit("lights/SWITCH_R_COLOUR", this.SwitchRColour);
                this.$store.commit("lights/SWITCH_G_COLOUR", this.SwitchGColour);
                this.$store.commit("lights/SWITCH_B_COLOUR", this.SwitchBColour);
                this.$store.commit("lights/SWITCH_COLOUR", SwitchColour);
                this.RemoteControlModeFlag = Number(lightsInfo.RemoteControlMode);
                this.$store.commit('change_switchLightInfo',{});//云端 app端相等，置空
                console.log('getLightsCallback light switch 之前修改过，并且返回数据与本地数据相等，清除本地数据，以云端数据为基准')
              } else {
                console.log('getLightsCallback light switch 之前修改过，但是云端数据与本地数据不一致，以本地数据为基准')
              }
            } else {
              if(this.lightSwitchDataCanUpdate){
                //单温区 台下单，台下双
                this.SaveSwitchMode = Number(lightsInfo.SaveSwitchMode) == 1 ? true : false;
                this.SwitchModeOntime = Number(lightsInfo.SwitchModeOntime) || 0;
                console.log("getLightsCallback~~~返回的数据~~~SwitchModeOntime", this.SwitchModeOntime);
                if(this.SwitchModeOntime == 6){
                  this.SwitchModeOntimeText = this.presenseSensorDisplayList[0];
                }else{
                  this.SwitchModeOntimeText = this.presenseSensorDisplayList[this.SwitchModeOntime + 1];
                }
                this.SwitchLightsIntensity = Number(lightsInfo.SwitchLightsIntensity) || 0;
                if (this.SwitchLightsIntensity > 0) {
                  this.lightsSwitchOnoff = true;
                }
                this.SwitchLightsColour = Number(lightsInfo.SwitchLightsColour) || 0;
                this.RemoteControlModeFlag = Number(lightsInfo.RemoteControlMode);
                this.$store.commit('change_switchLightInfo',{});//云端 app端相等，置空
                console.log('getLightsCallback light switch 之前修改过，并且返回数据与本地数据相等，清除本地数据，以云端数据为基准')
              } else {
                console.log('getLightsCallback light switch 之前修改过，但是云端数据与本地数据不一致，以本地数据为基准')
              }
            }
            // this.$store.commit('change_switchLightInfo',val.data);
            // console.log('获取灯光信息回调---state',JSON.stringify(this.switchLight_Info))
          }
        }
      } else {
        this.$toast(this.$t("lang.failure"));
      }
     console.log('getLightsCallback -------------------------end-------------------------')
    },
    sendCmdCallback(data) {
      this.showLoading = false;
      console.log("sendCmdCallback返回数据_newLights页面", data);
      if (data.resultCode == "0" || data.resultCode == 0) {
        console.log('switchLightInfo==',this.$store.state.switchLightInfo)
        this.$store.state.switchLightInfo.SaveSwitchMode = this.SaveSwitchMode;
        this.$store.state.switchLightInfo.SwitchLightsIntensity = this.SwitchLightsIntensity;
        this.$store.state.switchLightInfo.SwitchLightsColour = this.oneZoneFlag == 0 ? 23 - Number(this.SwitchLightsColour) : Number(this.SwitchLightsColour);
        if(this.oneZoneFlag == 0){
          this.$store.state.switchLightInfo.SwitchRGBLightIntensity = this.SwitchRGBLightIntensity;
          this.$store.state.switchLightInfo.SwitchRColour = this.SwitchRColour;
          this.$store.state.switchLightInfo.SwitchGColour = this.SwitchGColour;
          this.$store.state.switchLightInfo.SwitchBColour = this.SwitchBColour;
        }
        this.$store.state.switchLightInfo.SwitchModeOntime = this.SwitchModeOntime;
        for(var initDSD in this.switchLight_Info){
          if(this.initDataStateData[initDSD].length <= 3){
            if(this.initDataStateData[initDSD][1] == undefined){
              console.log('getLightsCallback initDataStateData state 存数据 [1] 为空，存入state值')
              this.initDataStateData[initDSD].push(this.switchLight_Info[initDSD])
            }
            if( this.initDataStateData[initDSD][1] != undefined && this.initDataStateData[initDSD][1] !== this.switchLight_Info[initDSD]){
              console.log('getLightsCallback initDataStateData state 存数据 [1] 为空，存入state值')
              this.initDataStateData[initDSD][1] = this.switchLight_Info[initDSD]
            }
          }
        }
        console.log('getLightsCallback sendCmdCallback initDataStateData',JSON.stringify(this.initDataStateData))
        console.log('getLightsCallback  sendCmdCallback this.switchLight_Info',JSON.stringify(this.switchLight_Info))
        this.$toast(this.$t("lang.success"));
      } else {
        this.showLoading = false;
        this.$toast(this.$t("lang.failure"));
        this.getLightsInfo();
      }
    },
    //物理返回键
    onBackPressedCallback() {
      this.goBack();
    },
  },
  components: {
    commonHeader: () => import("@/components/common/commonHeader"),
    commonBottom: () => import("@/components/common/commonBottom"),
    loading: () => import("@/components/common/loading"),
  },
};
</script>
<style lang="less">
/* 顶部标题右移35px 原始位置35px 共计70px */
#wine_lights .common_header_center {
  left: 70px;
  font-size: 16px;
  color: #000;
  font-weight: 500;
  font-family: ProximaNova-Medium;
}
/* 头部 */
.line {
  width: 342px;
  height: 1px;
  background: #979797;
  margin: 36px 0;
  margin-left: 32px;
}
#wine_lights {
  height: 100vh;
}
/* tab切换 */
#wine_lights .lights_tab_card {
  position: fixed;
  top: 78px;
  right: 0;
  left: 0;
  width: 100%;
  height: 64px;
  background: #000;
  z-index: 2;
  .tab_card_title {
    width: 100%;
    height: 64px;
    background: #000;
    /*overflow: hidden;*/
    li {
      float: left;
      width: 50%;
      height: 64px;
      text-align: center;
      line-height: 64px;
      font-size: 15px;
      color: #7b7b7b;
      font-weight: bold;
      // font-family: Arial;
      cursor: pointer;
      white-space:normal;
      /*white-space:nowrap;*/
    }
    .active {
      color: #ffffff;
    }
    .twoRow{
      line-height: 32px;
    }
  }
}
.onTime {
  padding-bottom: 60px;
}
/* 主体 */
#wine_lights .lights_tab_card_content {
  position: fixed;
  top: 132px;
  left: 0;
  right: 0;
  bottom: 0px;
  padding-top: 40px;
  color: #000;
  font-size: 16px;
  // font-family: Arial;
  background: #e9e9e9;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
  .lights_content {
    width: 100%;
    overflow: hidden;
    .lights_switch {
      width: 83%;
      height: 39px;
      margin: 0 auto 10px;
      .lights_switch_title {
        float: left;
        width: 80%;
        height: 39px;
        line-height: 39px;
        font-size: 20px;
        // font-family: Arial;
        color: #000;
        font-weight: bold;
      }
      .lights_switch_btn {
        float: right;
        width: 17%;
        height: 39px;
        padding-top: 4px;
      }
    }
    .lights_item {
      width: 83%;
      margin: 10px auto 0;
      .lights_title {
        width: 100%;
        margin-bottom: 19px;
        font-size: 18px;
        color: #000;
        // font-family: Arial;
        font-weight: 800;
      }
      .lights_bright_percent {
        text-align: right;
        width: 100%;
        margin-bottom: 10px;
        font-size: 18px;
        font-weight: 800;
        // font-family: Arial;
      }
      .lights_bright_percent_show {
        display: flex;
        margin-top: 5px;
        justify-content: space-between;
        align-items: center;
        span {
          font-size: 10px;
          font-weight: 700;
          // font-family: Arial;
        }
      }
      .lights_disable_block {
        width: 100%;
        height: 24px;
        margin: 0 auto;
        background: #c4c4c4;
        opacity: 0.3;
        filter: alpha(opacity=30);
        -webkit-border-radius: 12px;
        -moz-border-radius: 12px;
        -ms-border-radius: 12px;
        -o-border-radius: 12px;
        border-radius: 12px;
      }
      .lights_time {
        width: 100%;
        font-size: 16px;
        // font-family: Arial;
      }
      .ambient_color {
        width: 100%;
        height: 24px;
        margin: 0 auto;
        -webkit-border-radius: 12px;
        -moz-border-radius: 12px;
        -ms-border-radius: 12px;
        -o-border-radius: 12px;
        border-radius: 12px;
        cursor: pointer;
      }
    }
    .lights_item_ambient {
      margin: 24px auto 0;
    }
  }
}
#wine_lights .lights_slider_wrap {
  width: 100%;
  height: 24px;
  -webkit-border-radius: 12px;
  -moz-border-radius: 12px;
  -ms-border-radius: 12px;
  -o-border-radius: 12px;
  border-radius: 12px;
}
#wine_lights .van-switch {
  width: 51px;
  height: 30px;
  -webkit-border-radius: 15px;
  -moz-border-radius: 15px;
  -ms-border-radius: 15px;
  -o-border-radius: 15px;
  border-radius: 15px;
  border: 1px solid #000;
}
#wine_lights .van-switch__node {
  top: 0;
  left: -1px;
  width: 30px;
  height: 30px;
  border: 1px solid #000;
}
#wine_lights .van-switch--on .van-switch__node {
  -webkit-transform: translateX(23px);
  transform: translateX(23px);
}
#wine_lights .van-slider {
  width: 87%;
  height: 24px;
  margin: 0 auto;
  background: none;
}
#wine_lights .van-slider .lights_button {
  position: relative;
  width: 40px;
  height: 24px;
  -webkit-border-radius: 12px;
  -moz-border-radius: 12px;
  -ms-border-radius: 12px;
  -o-border-radius: 12px;
  border-radius: 12px;
}
.light_mode_box {
  width: 80%;
  margin: 0 auto;
  h3 {
    padding-bottom: 25px;
  }
  ul {
    margin-bottom: 30px;
    li {
      display: flex;
      justify-content: space-between;
      height: 60px;
      line-height: 60px;
      // padding:20px 0 ;
      .light_icon {
        font-size: 36px;
        padding-right: 10px;
      }
      span {
        width: 100%;
        font-size: 20px;
        font-weight: 400;
        // padding-left: 10px;
      }
    }
  }
}
</style>



